<template>
    <div style="margin:30px">
        <el-divider></el-divider>
        <el-descriptions class="margin-top" title="A/B在线实验" :column="3" :size="size">
             <template slot="extra">
                 <router-link :to="{ name: '营销集市', params: {campaign_id: this.$route.params.campaign_id}}" style='text-decoration: none;color: aliceblue;' align="right"> 
                    <el-button class="" ><i class="el-icon-arrow-left" style="margin-right: 10px;"></i>返回营销集市</el-button>
                </router-link>
            </template>
            <el-descriptions-item label="实验所属活动">{{CampaignMetaData.campaign.name}}</el-descriptions-item>
            <el-descriptions-item label="创建人">{{CampaignMetaData.campaign.author}}</el-descriptions-item>
            <el-descriptions-item label="创建时间">
                 <timeago :datetime="CampaignMetaData.campaign.creation_date" locale="zh-CN"></timeago>
            </el-descriptions-item>            
            <el-descriptions-item label="已创建的实验数">
                {{CampaignMetaData.strategy_cnt}}
            </el-descriptions-item>
            <el-descriptions-item label="是否公开">
                <el-switch
                           v-model="value1"
                           active-color="#13ce66"
                           inactive-color="#ff4949">
                </el-switch>
            </el-descriptions-item>
            <el-descriptions-item label="活动来源">
                <el-tag size="small" type="warning">我的活动</el-tag>
            </el-descriptions-item>
        </el-descriptions>

        <el-collapse v-model="activeNames">
            <el-collapse-item title="活动描述" name="1">
                <div> 
                    <slot :description="description" class="main-a">
                    <span v-show="!editModelTaskVisible">{{CampaignMetaData.campaign.description}}</span>
                    <el-input v-model="description" type="textarea" 
                              :autosize="{ minRows: 4, maxRows: 6}"
                              class="modelDescStyle"
                              v-show="editModelTaskVisible">
                    </el-input>
                    <el-button  v-show="!editModelTaskVisible" class="inline" icon="el-icon-edit" type="text"  @click="handleTaskInfoEdit('index',{description:'description'})" style="padding: 0px; margin-right: 10px;">
                    </el-button>
                    <br>
                     <span v-show="editModelTaskVisible">
                        <el-button type="text"  @click.native="editModelTaskVisible = false" icon="el-icon-back">取消</el-button>
                        <el-button type="text" @click.native="editSubmit" :loading="editLoading" icon="el-icon-check">保存</el-button>
                    </span>
                   </slot>
                </div>
              </el-collapse-item>
          <el-collapse-item title="实验列表" name="2">
              <!--工具条-->
              <el-col :span="24" class="toolbar" style="padding-bottom: 10px;">
                  <el-form :inline="true" :model="filters" @submit.native.prevent>
                      <el-form-item>
                          <el-input v-model.trim="filters.name" placeholder="名称" @keyup.enter.native="getModelTaskList"></el-input>
                      </el-form-item>
                      <el-form-item>
                          <el-button type="primary" @click="getModelTaskList">查询</el-button>
                      </el-form-item>
                      <el-form-item>
                          <el-button type="primary" @click="handleAdd" icon="el-icon-plus">A/B实验</el-button>
                      </el-form-item>
                  </el-form>
              </el-col>
              <!--列表-->
              <el-table :data="abexpList"
                        highlight-current-row
                        v-loading="listLoading"
                        @selection-change="selsChange" style="width: 100%;">
                  <el-table-column type="selection" min-width="5%">
                  </el-table-column>
                  <el-table-column prop="name" label="实验名称" min-width="20%" sortable show-overflow-tooltip>
                      <template slot-scope="scope">
                          <img v-show="!scope.row.status" src="@/assets/icon-yes.svg"/>
                          <img v-show="scope.row.status" src="@/assets/icon-no.svg"/>
                          &nbsp;
                          <el-icon name="name"></el-icon>
                                      <router-link :to="{ name: '实验组信息', params: {ab_id: scope.row.id,campaign_id:CampaignMetaData.campaign.id}}"
                                       style='text-decoration: none;'>{{ scope.row.name }}
                          </router-link>
                      </template>
                  </el-table-column>
                  <el-table-column  prop="id" label="实验编号" min-width="15%" sortable show-overflow-tooltip>
                  </el-table-column>
                  <el-table-column prop="abalt_cnt" label="实验组数" min-width="15%" sortable>
            </el-table-column>
                  <el-table-column prop="creation_date" :formatter="formatTime" label="创建时间" min-width="15%" sortable show-overflow-tooltip>
                    <template slot-scope="scope">
                        <timeago :datetime="scope.row.creation_date" locale="zh-CN"></timeago>
                    </template>
                  </el-table-column>
                   <el-table-column prop="abalt_cnt" label="曝光数" min-width="15%" sortable show-overflow-tooltip>
                  </el-table-column>
                  <el-table-column prop="description" label="实验描述" min-width="30%" sortable show-overflow-tooltip> </el-table-column>
                
                  <el-table-column label="操作" min-width="25%">
                      <template slot-scope="scope">
                        <el-button size="small" v-if="scope.row.abalt_cnt===0" type="primary" @click="addAltList(scope.$index, scope.row)">+ 实验组</el-button>
                        <el-button size="small" v-if="scope.row.abalt_cnt>0" type="info" @click="addAltList(scope.$index, scope.row)">+ 实验组</el-button>
                        <el-button size="small" type="danger" @click="handleJupyter(scope.$index, scope.row)">删除</el-button>
                        <!--<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>-->
                      </template>
                   </el-table-column>
              </el-table> <!--工具条-->
              <el-col :span="24" class="toolbar">
                  <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">删除实验</el-button>
                  <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
                  </el-pagination>
              </el-col>
           </el-collapse-item>
       </el-collapse>


        <!--注册界面-->
        <el-dialog title="选择环境" :visible.sync="regFormVisible" :close-on-click-modal="false" style="width: 60%; left: 20%" inline="true">
            <el-form :model="regForm"  :rules="regFormRules" ref="regForm" label-width="80px">
                <el-form-item label="环境名称" prop="workspace_id">
                    <el-select v-model="regForm.workspace_id" 
                               filterable 
                               remote
                               :remote-method="selectWorkspaces"
                               :loading="getJupterByTaskidLoading"
                               placeholder="请选择环境" style="width:90%">
                        <el-option
                                   v-for="item in workspaces"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="regFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="linkworkspaceSubmit" :loading="regLoading">提交</el-button>
            </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog title="创建新实验" inline="true" :visible.sync="addFormVisible" :close-on-click-modal="false" style="width: 60%; left: 20%">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="实验名称" prop="name">
                    <el-input v-model.trim="addForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="实验描述" prop='description'>
                    <el-input type="textarea" :rows="3" v-model="addForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import { test, getAbExps, regModelToTask, addAbExp, getCreatedModels4queryAPI } from '@/api/api'
import moment from 'moment'
// eslint-disable-next-line no-unused-vars
import $ from 'jquery'
export default {
  data () {
    return {
        value22: [],
        is_public:true,
        value1: true,
        CampaignMetaData:{},
        linked_msg:'',
        task_id:0,
        workspace_id:1,
        getJupterByTaskidLoading:false,
        filters: {
            name: ''
        },
        active: 0,
        size: 'medium',
        getCreatedTasksLoading: false,
      CreatedTasks:[],
        workspaces:[],
        workspaceList:[],
      activeNames: ['2'],
      description:'策略库存储策略类别，具体的策略在子策略页面配置',
      abexpList: [],
      sche_index: 3,
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], // 列表选中列
      editModelTaskVisible: false,
      regFormVisible: false, // 编辑界面是否显示
      regLoading: false,
      regFormRules: {
        workspace_id: [
          { required: true, message: '请选择环境', trigger: 'blur' }
        ]
      },
      // 编辑界面数据
      regForm: {
        model_id: '',
        model_name:'',
        description: ''
      },
      editModelForm:{
          description: ''
      },
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        strategys: [
          { required: true, message: '请输入策略名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入策略描述', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        name: '',
          strategys:[],
        description: ''
      }

    }
  },
  created () {
      // 如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
      // this.form.Tasks = this.Tasks[0].id
  },
  methods: {
      //显示新增界面
      handleAdd: function () {
          this.addFormVisible = true;
      },
      next() {
          if (this.active++ > 3) this.active = 0;
      },
      getCampaignMetaData () {
          let self = this
          $.ajax({
              type: 'get',
              url: test + '/api/campaigns/get_campaign_metadata',
              async: true,
              data: { campaign_id: this.$route.params.campaign_id},
              headers: {
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: function (data) {
                  if (data.code === '999999') {
                      self.CampaignMetaData = data.data.data
                      console.log(self.CampaignMetaData,"self.CampaignMetaData")
                      // self.ApiList = data.data.data
                  } else {
                      self.$message.error({
                          message: data.msg,
                          center: true
                      })
                  }
              }
          })
      },
      getJupterByTaskid (task) {
          let self = this
          // this.getJupterByTaskidLoading = true
          $.ajax({
              type: 'get',
              url: test + '/api/workspaces/get_workspace_list',
              async: true,
              data: { project_id: this.$route.params.project_id,
                     user_key: JSON.parse(sessionStorage.getItem('name'))},
              headers: {
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000,
              success: function (data) {
                  if (data.code === '999999') {
                      // this.getJupterByTaskidLoading = false
                      self.workspaceList = data.data.data
                      console.log(self.workspaceList,"self.getJupterByTaskid")
                      
                  } else {
                      self.$message.error({
                          message: data.msg,
                          center: true
                      })
                  }
              }
          })
      },
    selectWorkspaces (query) {
      if (query !== '') {
        this.getJupterByTaskidLoading = true
        setTimeout(() => {
          this.getJupterByTaskidLoading = false
          this.workspaces = this.workspaceList.filter(item => {
            return item.name.toLowerCase()
              .indexOf(query.toLowerCase()) > -1
          })
        }, 200)
      } else {
        this.workspaces = []
      }
    },
    // 获取已经创建的模型信息
    getCreatedModels (task) {
      this.getCreatedModelsLoading = true
      let self = this
      let params = {
        project_id: this.$route.params.project_id,
        task_id: task,
        user_key: JSON.parse(sessionStorage.getItem('name'))
      }
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getCreatedModels4queryAPI(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.getCreatedModelsLoading = false
        if (code === '999999') {
          this.getCreatedModelList = data.map(item => {
            // console.log(item, 'itemmmmmm')
            return { value: `${item.value}`, label: `${item.key}` }
          })
            // 检索前显示全部 检索时使用函数 selectCreatedTasks
            if (this.getCreatedModelList.length<1){
                this.CreatedTasks = []
            }else{
                this.CreatedTasks = this.getCreatedModelList
            }
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 显示编辑界面
    handleTaskInfoEdit: function (index, row) {
      // console.log(row,this.description,"ddddd")
      this.editModelTaskVisible = true
      let desc = this.description
      // this.editForm = Object.assign({}, row);
      this.editModelForm = Object.assign({}, { description: desc })
    },
    
    formatTime: function (row, column) { // 日期格式化
      var date = row[column.property]
      if (typeof date === 'undefined') {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },

    // 获取ab列表
    getAbExpList () {
      this.listLoading = true
      let self = this
      let params = {
        campaign_id: this.$route.params.campaign_id,
        page: self.page,
        user_key: JSON.parse(sessionStorage.getItem('name')),
        name: self.filters.name
      }

      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getAbExps(headers, params).then(_data => {
        let { msg, code, data } = _data
        self.listLoading = false
        if (code === '999999') {
          self.total = data.total
          self.abexpList = data.data
          // self.project[self.sche_index].results = "data.data"
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该记录吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = {
          project_id: Number(this.$route.params.project_id),
          table_name: row.tableName,
          ids: [row.id]
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        console.log(params, 'paramsparamsparamslplp')
        delSampleTable(headers, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getSampleList()
        })
      })
    },

    handleCurrentChange (val) {
      this.page = val
      this.getSampleList()
    },
    // 显示注册界面
    linkJupyter: function (index, row) {
      this.regFormVisible = true
      this.regForm = Object.assign({}, row)
      //  console.log(row.id,"rowrowrowrow")
      this.getJupterByTaskid(row.id)
        this.task_id = row.id
      // console.log(row.dataSpace,"row.dataSpace")
    },
      findWorkspaceByTask:function(workspace){
          let self = this
          // this.getJupterByTaskidLoading = true
          $.ajax({
              type: 'get',
              url: test + '/api/workspaces/get_workspace_bytask',
              async: true,
              data: { task_id: workspace,
                     user_key: JSON.parse(sessionStorage.getItem('name'))},
              headers: {
                  Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
              },
              timeout: 5000
          }).then(
              (responseText)=>{
                  if (responseText.code==='999998'){
                      this.$notify({
                        title: '提示',
                        message: '请先连接环境',
                        type: 'warning'
                    })
                  }
                  this.workspace_id = responseText.data.data
                  this.$router.push(
                      {
                          name: '研究空间', params:
                          {
                              workspace_id: this.workspace_id//this.$route.params.project_id,
                          }
                      })
              },//成功就调用这个函数
              (request)=>{console.log(request);})//失败就调用这个函数
      },
      handleJupyter: function(index,row){
          this.findWorkspaceByTask(row.id)
          console.log(this.workspace_id,"this.workspace_idthis.workspace_id")
          
      },
    // 显示新增界面
    addAltList:function(index,row) {
        console.log(row,'rowrowrow')
        if (row.abalt_cnt>0){
            this.$notify({
                title: '提示',
                message: '实验组已建立',
                type: 'warning'
            })
        }else{
            this.$router.push(
                {name: '创建实验服务',
                 params: {ab_id: row.id,
                          campaign_id:this.$route.params.campaign_id}
                }) 
                }
        
     // this.addFormVisible = true
    },
     
    getModelTaskStatus: function(val) {
      return val
  },
      // 编辑
    linkworkspaceSubmit: function () {
        let self = this
        this.loadworkspace= true
        let param = JSON.stringify({ project_id: this.$route.params.project_id,
                                    task_id: this.task_id,
                                    workspace_id:this.regForm.workspace_id,
                                    user_key: JSON.parse(sessionStorage.getItem('name'))})
        console.log(param,"ffffsf")
      $.ajax({
        type: 'POST',
        url: test + '/api/models/link_task_to_workspace',
        async: true,
        data: param,
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: (data) => {
            self.$refs['regForm'].resetFields()
            self.regFormVisible = false
          if (data.code === '999999') {
              if (data.data.data.linked==="1"){
                  this.linked_msg="环境已存在"
              }else{
                  this.linked_msg="连接成功"
              }
              console.log(data.data.data.linked,"ddff")
            self.$message({
              message: this.linked_msg,
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        console.log(valid)
        if (valid) {
          let self = this
          this.$confirm('确认创建实验吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = {
                campaign_id: Number(this.$route.params.campaign_id),
                name:self.addForm.name,
                description: self.addForm.description,
                user_key: JSON.parse(sessionStorage.getItem('name'))
            }
            console.log(params, 'paramsparamsparamsparamsparamsparams')
            let headers = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addAbExp(headers, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '实验创建成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getAbExpList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getAbExpList()
              }
            })
          })
        }
      })
    },
    selsChange: function (sels) {
      this.sels = sels
    },
    // 批量删除
    batchRemove: function () {
      let ids = this.sels.map(item => item.id)
      let self = this
      this.$confirm('确认删除选中记录吗？', '提示', {
        type: 'warning'
      }).then(() => {
        self.listLoading = true
        // NProgress.start();
        let params = {
          project_id: Number(this.$route.params.project_id),
          ids: ids
        }
        let headers = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        // eslint-disable-next-line no-undef
        delHost(headers, params).then(_data => {
          let { msg, code } = _data
          self.listLoading = false
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getBaseTaskJob()
        })
      })
    },
      handlePublic:function(){
          this.is_public = this.value1
          console.log("valye",this.is_public)
      }
  },
    watch:{
        value1(){
            this.handlePublic()
        }
        // 
    
},
  mounted () {
      this.getCampaignMetaData()
      this.getAbExpList()
  }
}
</script>
